import React, {useState, useEffect} from 'react';
import { IoIosFlask } from "react-icons/io";
import {Form, Button, Icon} from './styles'

const Lab = ({ history }) => {
    const [start, setStart] = useState(false)
    const {x, y} = history.location.state
    const {innerHeight, innerWidth} = window;

    const centerX = innerWidth / 2 - 20;
    const centerY = innerHeight / 2 - 35;

    useEffect(() => {
        setTimeout(() => {
            setStart(true)
        }, 500);
    },[])

    const startAnimation = {
        transform: `translate(${x}px, ${y}px)`
        
    }
    const endAnimation = {
        transform: `translate(${centerX}px, ${centerY}px)`
    }

    return (
        <Form style={{ height: start ? innerHeight + "px" : "450px" }}>
            <div className="form-group">
                <Icon style={start ? endAnimation : startAnimation}>
                    <IoIosFlask />
                </Icon>
                <Button>Entrar no Lab</Button>
            </div>
        </Form>
    )
}

export default Lab