import React, { useState, useEffect } from "react";
import axios from "axios";
import { Transition, Header, Main, Footer } from "./styles";
import { motion } from "framer-motion";
import {
  IoMdMail,
  IoLogoLinkedin,
  // IoIosFlask,
  IoMdArrowDroprightCircle
} from "react-icons/io";

export default ({ history }) => {
  const [items, setItems] = useState([]);
  const [active, setActive] = useState(false);

  useEffect(() => {
    async function getData() {
      const res = await axios.get("https://gist.githubusercontent.com/rodrigoant/f906faf135145c885f3bcc4e1c944571/raw/");
      setItems(res.data);
    }
    getData();
  }, []);

  // const goToLab = e => {
  //   setActive(true);
  //   setTimeout(() => {
  //     history.push("/lab", {
  //       x: xPos,
  //       y: yPos
  //     });
  //   }, 1300);
  // };

  return (
    <>

      <Transition className={active && "active"}>
        {active && 

          <div className="form-group">
            <div className="form-control">
              <input />
              <button>
          
                Entrar
              </button>
            </div>
            <div className="form-control">
              <svg width="50" height="50" viewBox="0 0 120 120">
                  <circle
                    cx="60"
                    cy="60"
                    r="54"
                    fill="none"
                    stroke="#e6e6e6"
                    strokeWidth="10"
                  />
              </svg>
            </div>
            <div className="form-control">
              <button onClick={() => setActive(false)}>
                Voltar
              </button>
            </div>
          </div>
         
        }
      </Transition>
    
        <Header
          initialPose="exit"
          pose="enter"
        >
          <motion.h1
            initial={{ y: 15, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0, duration: 0.5 }}
          >
              <span>
                <strong>Olá</strong>, meu nome é <strong>Rodrigo Antunes</strong>
              </span>
          </motion.h1>
          <motion.h3
            initial={{ y: 15, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.2, duration: 0.5 }}
          >
            <span>Desenvolvedor Front-End</span>
          </motion.h3>
          <motion.div
            className="description"
            initial={{ y: 15, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ delay: 0.4, duration: 0.5 }}
          >
          <span>
            <p>
              Trabalho há {new Date().getFullYear() - 2009} anos com Desenvolvimento Web.
            </p>
            <p>
              Gosto de criar experiências interativas com HTML, CSS e Javascript
            </p>
          </span>
        </motion.div>
        <div className="contato">
          <ul>
            <motion.li
              initial={{ y: 40, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 1.5, duration: 0.5 }}
            >
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="https://www.linkedin.com/in/rodrigoantunespaiva/"
              >
                <IoLogoLinkedin className={"icon-logo"} />
              </a>
            </motion.li>
            <motion.li
              initial={{ y: 40, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 1.6, duration: 0.5 }}
            >
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="mailto:contatorodrigoantunes@gmail.com"
              >
                <IoMdMail className={"icon-logo"} />
              </a>
            </motion.li>
            {/* <motion.li
              // style={{ display: "none" }}
              initial={{ y: 40, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 1.7, duration: 0.5 }}
              onClick={goToLab}
            >
              <IoIosFlask className={"icon-logo"} />
            </motion.li> */}
          </ul>
        </div>
      </Header>

      {!active && 
        <Main initialPose="exit" pose="enter">
          {items.map(item => (
            <section
              key={item.id}
              style={{
                backgroundColor:
                  item.id % 2 !== 0 ? "rgb(250,250,250)" : "rgb(240,240,240)"
              }}
            >
              <motion.div
                className="container"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.8, duration: 0.5 }}
              >
                <div className="left-container">
                  <a
                    title={item.name}
                    target="_blank"
                    rel="noopener noreferrer"
                    href={item.link}
                  >
                    <img src={item.image} alt={item.name} />
                  </a>
                </div>
                <div className="right-container">
                  <div className="client-name">{item.name}</div>
                  <div className="client-info">{item.job}</div>
                  <div className="client-desc">{item.description}</div>
                  <div className="client-link">
                    <a
                      title={item.name}
                      target="_blank"
                      rel="noopener noreferrer"
                      href={item.link}
                    >
                      <IoMdArrowDroprightCircle />
                      Acesse o projeto
                    </a>
                  </div>
                </div>
              </motion.div>
            </section>
          ))}
        </Main>
        }
        {!active && 
        <Footer initialPose="exit" pose="enter">
          {new Date().getFullYear()} | Rodrigo Antunes
        </Footer>
        }
    </>
  );
};
